//  orderData:
// {
//   "success": true,
//   "name": " spicy  space ",
//   "order": {
//       "number": 5373
//   }
// }
const orderData = {};

function reducerOrderData(state = orderData, action) {
  switch (action.type) {
    case 'SAVE_ORDER':
      return action.orderData;
    default:
      return state;
  }
};

export { reducerOrderData };
