import styles from './MainSection.module.css';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { fetchIngredients } from '../../store/actions/ingredientsListActions';
import { BurgerIngredients } from '../BurgerIngredients/BurgerIngredients';
import { BurgerConstructor } from '../BurgerConstructor/BurgerConstructor';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

function MainSection() {
  //    ,        
  const ingredientsList = useSelector((store) => store.ingredientsListStore);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchIngredients());
  }, []);

  //       AppHeader  MainSection        
  //    .
  // ,  ingredientsList  true (..    ) 
  //      (.. ingredientsList ) -
  if (!ingredientsList || ingredientsList.length === 0) {
    return (
      // -        .
      <div className={styles.centered_text}>
        <h1>Loading ingredients list from server...</h1>
      </div>
    );
  }
  //      ingredientsList ,  AppHeader  MainSection  .
  return (
    //       drag and drop
    //     ,    
    <DndProvider backend={HTML5Backend}>
      <main className={styles.main}>
        <BurgerIngredients />
        <BurgerConstructor />
      </main>
    </DndProvider>
  );
}

export { MainSection };
